*{
  font-family: Verdana, sans-serif;
}

p{
  margin: 0;
}

p > a, .blue-link{
  color: rgb(98 184 245) !important;
}


h3, h2{
  margin: 10px 0 10px 0;
}

:root{
  --nav-text-color: white;
}

.simple-center{
  display: flex;
  align-items: center;
  justify-content: center;
}

html{
  width: 100%;
  height: 100%;
  overflow: hidden;
}

body{
  background: linear-gradient(180deg, rgb(10, 10,10), rgb(54, 54, 54));
  width: 100%;
  height: 100%;
}

#navigation{
  top: 0;
  left: 0;
  border-bottom: 1px solid #000;
  height: 70px;
  width: 100%;
  position: fixed;
  display: flex;
  box-sizing: border-box;
  background-color: #26313e;
  background-color: #1f4674;
  color: var(--nav-text-color);
}


.nav-inner{
  flex: 1 1 0;
  /*border-right: 1px solid #000;*/
}

#nav1{
  display: flex;
  align-items: center;
  padding-left: 50px;
}

#nav1 text{
  font-size: 30px;
}

#nav3{
  padding-right: 50px;
}

#nav2{
  flex: 0 0 max-content;
}

.navigation-tab{
  height: 100%;
  /*border: 1px solid #000;*/
  font-size: 22px;
  cursor: pointer;
  flex-direction: column;
}

#main{
  position: absolute;
  left: 0;
  top: 70px;
  width: 100%;
  min-width: 100%;
  min-height: calc(100% - 70px);
  height: calc(100% - 70px);
  overflow-y: auto;
}

.nav-tab{
  flex: 1 1 0;
  width: 0;
  transition: width 0.5s;
  border-top: 2px solid var(--nav-text-color);
}

.navigation-tab > text{
  padding: 0 10px;
}

.navigation-tab:hover .nav-tab-lower{
  width: calc(100% - 20px)
}

.navigation-tab.not-selected-tab:hover > text{
  color: rgb(235, 186, 52);
}

.navigation-tab.not-selected-tab:hover .nav-tab-lower{
  border-color: rgb(235, 186, 52);
}

.navigation-tab.not-selected-tab .nav-tab-lower{
  /*border-color: rgba(0, 0, 0, 0);*/
}

.navigation-tab.selected-tab .nav-tab-lower{
  width: calc(100% - 20px);
  transition: width 0s;
}

.no-display{
  display: none !important;
}

.navigation-content{
  min-width: 100%;
  min-height: 100%;
}

.navigation-page{
  box-sizing: border-box;
  width: 100%;
  min-width: 100%;
  height: 100%;
  min-height: 100%;
  padding: 50px 0 100px 0;
  display: flex;
  flex-direction: column;
  gap: 35px;
}

#about-page{
  /*background-color: rgb(210, 234, 255);*/
  /*background: radial-gradient(circle, rgba(119,119,119,0.9976365546218487) 0%, rgba(52,52,52,1) 86%);*/
}

.navigation-page *{
  color: #fff;
}

.about-section{
  box-sizing: border-box;
  width: 100%;
  max-width: 950px;
  padding: 0 0 0 200px;
}

.about-section > h1{
  font-size: 40px;
}


.about-section > p, .about-sub-section > p, .about-sub-section li{
  /*max-width: 50%;*/
}

.about-section.flexed{
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.about-sub-section{
  
}

.about-span{
  cursor: pointer;
  text-decoration: underline;
  color: rgb(98 162 245);
}

.projects-page{
  display: flex;
  flex-direction: column;
}

.about-section.about-section.flexed > h1{
  margin: 0;
}

.challenge-list > li{
  margin-bottom: 15px;
}

.challenge-list > li:last-child{
  margin-bottom: 0;
}

.social{
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
}

.about-section > h2{
  margin: 0 0 -10px 0;
}

#resultsnet{
  width: 100%;
  margin-bottom: 15px;
}

.project-image{
  width: 100%;
  margin-bottom: 15px;
}

@media only screen and (max-width: 800px){
  .about-section{
    padding: 0 50px 0 50px;
  }
}

@media only screen and (max-width: 449px){
  #myname{
    display: none;
  }
}